/* Grid */
.container {
  max-width: 49rem; }

.w-1\/5 {
  width: 20%; }

.w-1\/4 {
  width: 25%; }

.w-1\/3 {
  width: 33.3333%; }

.w-1\/2 {
  width: 50%; }

.w-full {
  width: 100%; }

.h-full {
  height: 100%; }

/* Colors */
body {
  background-color: #FFFFFF;
  color: #4A4A4A; }

button.bg-blue:hover {
  background-color: #001e3a; }

button.bg-gray:hover {
  background-color: #171717; }

button.bg-white:hover {
  background-color: #cccccc; }

input[disabled],
button[disabled],
textarea[disabled] {
  opacity: 0.5; }

a {
  color: #4A4A4A; }

a:active, a:hover {
  color: #171717; }

a:visited {
  color: #6A3FC4; }

a:visited:hover {
  color: #402578; }

/*
Only the resets are affected by the Dark Mode modifier. Other elements within
sections on dark backgrounds will be styled by applying dark-mode-approved
classes from the reusable class library.
*/
.dark-mode {
  color: #FFFFFF; }
  .dark-mode a {
    color: #FFFFFF; }
  .dark-mode a:active, .dark-mode a:hover {
    color: #cccccc; }
  .dark-mode a:visited {
    color: #E6E6E6; }
  .dark-mode a:visited:hover {
    color: #b3b3b3; }

.bg-blue {
  background-color: #0054A0; }

.bg-gray {
  background-color: #4A4A4A; }

.bg-gray-light {
  background-color: #E6E6E6; }

.bg-orange {
  background-color: #F7841B; }

.bg-white {
  background-color: #FFFFFF; }

.border-0 {
  border: 0; }

.border-gray {
  border: 1px solid #4A4A4A; }

.border-white {
  border: 1px solid #FFFFFF; }

.border-b-gray {
  border-bottom: 1px solid #4A4A4A; }

.border-b-white {
  border-bottom: 1px solid #FFFFFF; }

.font-color--blue {
  color: #0054A0; }

.font-color--gray {
  color: #4A4A4A; }

.font-color--green {
  color: #349D44; }

.font-color--red {
  color: #BD0606; }

.font-color--white {
  color: #FFFFFF; }

/* Typography */
.font-sans {
  font-family: 'Open Sans'; }

.font-serif {
  font-family: 'Arapey'; }

.font-0\.75 {
  font-size: 0.75rem; }

.font-0\.875 {
  font-size: 0.875rem; }

.font-1 {
  font-size: 1rem; }

.font-1\.25 {
  font-size: 1.25rem; }

.font-1\.5 {
  font-size: 1.5rem; }

.font-1\.75 {
  font-size: 1.75rem; }

.font-2 {
  font-size: 2rem; }

.font-2\.5 {
  font-size: 2.5rem; }

.font-2\.75 {
  font-size: 2.75rem; }

.font-size-xs {
  font-size: 0.75rem; }

.font-size-s {
  font-size: 0.875rem; }

.font-size-m {
  font-size: 1rem; }

.font-size-l {
  font-size: 1.25rem; }

.font-size-xl {
  font-size: 1.5rem; }

.font-size-2x {
  font-size: 1.75rem; }

.font-size-3x {
  font-size: 2rem; }

.font-size-4x {
  font-size: 2.5rem; }

.font-size-5x {
  font-size: 2.75rem; }

.bold {
  font-weight: bold; }

.italic {
  font-style: italic; }

.no-underline,
.no-underline:hover {
  text-decoration: none; }

.underline {
  text-decoration: underline; }

/* Position */
.absolute {
  position: absolute; }

.relative {
  position: relative; }

/* Display */
.hide {
  display: none; }

.flex {
  display: flex; }

.inline-flex {
  display: inline-flex; }

/* Flexbox */
.justify-center {
  justify-content: center; }

.justify-between {
  justify-content: space-between; }

.items-center {
  align-items: center; }

/* Overflow */
.overflow-hidden {
  overflow: hidden; }

/* Text Alignment */
.text-center {
  text-align: center; }

/* Border Radius */
.radius-5 {
  border-radius: 5px; }

.radius-1\/2 {
  border-radius: 50%; }

/* Whitespace */
.truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

/* Cursor */
.pointer {
  cursor: pointer; }

/* Reset */
html, body {
  box-sizing: border-box; }

* {
  box-sizing: inherit; }

body, button, input, textarea {
  font-family: 'Open Sans';
  font-size: 16px; }

body, div, h1, h2, h3, p {
  margin: 0;
  border: 0;
  padding: 0; }

button {
  cursor: pointer; }

